import {TextInput} from "./text-input";
import {InputAddon} from "./input-addon";
/**
 * @classdesc Input personalizado com Ã­cone de prcentagem.
 * 
 * @example
 * 
 * import {PercentInput} from "mvcomponents/input";
 * 
 * let percentInput:PercentInput = new PercentInput();
 * 
 * this.append(percentInput);
 * 
 * @export
 * @class PercentInput
 * @extends {TextInput}
 * @param {string} [p_text=null] Altera o valor do campo.
 * @memberof module:mvcomponents/input
 */
export class PercentInput extends TextInput{
	constructor(p_text:string=""){
		super(p_text);

		this
			.setSize(3)
			.setStyleName("InputPercent")
			.append(new InputAddon()
				.setText("%"));
	}
}
